import Vue from 'vue'
import Vuex from 'vuex'

import namesF from './random-names';

import MGame from '../monopoly';
import Screens from '../Screens';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {  
    currentComponent: Screens.STARTSCREEN,
    saleOnGameOver: true,
    isStarted: false,
    winner: null,    
    game: null,
    selectedCard: null,
  },
  mutations: { 
    switchSaleOnGameOver(state, payload){
      state.saleOnGameOver = payload;
    },
    openOptions(state){
      state.currentComponent = Screens.OPTIONSBOARD;
    },
    closeOptions(state){
      state.currentComponent = Screens.STARTSCREEN;
    },
    cancelBuyCard(state){
      state.currentComponent = Screens.AUCTIONBOARD;      
    },
    returnFromAuction(state){
      state.currentComponent = Screens.GAMEBOARD;      
    }, 
    startGame(state, playersCount){
      state.isStarted = true;
      state.winner = null;
      state.game = null;
      state.game = new MGame({maxPlayer: playersCount, saleOnGameOver: state.saleOnGameOver});      
      const names = namesF(playersCount);      
      for(let i = 0; i < playersCount; i++){
        state.game.createPlayer(names[i]);
      }
      state.currentComponent = Screens.GAMEBOARD;
    },
    selectCard(state, card){
      state.currentComponent = Screens.CARDOPTIONSBOARD;
      state.selectedCard = card;
    },
    clearSelectedCard(state){
      state.currentComponent = Screens.GAMEBOARD;
      state.selectedCard = null;
    },
    openSwap(state){
      state.currentComponent = Screens.SWAPBOARD;
    },
    closeSwap(state){
      state.currentComponent = Screens.GAMEBOARD;
    },
    win(state, player){
      state.selectedCard = null;
      state.winner = player;      
      state.currentComponent = Screens.STARTSCREEN;
      state.isStarted = false;
      state.game.clearGameData();           
    }
  },
  actions: {
    startTurn: (context) => context.state.game.go(),
    doAction: (context) => context.state.game.doNext(),
    endTurn: (context) => context.state.game.endTurn(),
    dontBuy: (context) => {
      context.commit('cancelBuyCard');
      context.state.game.cancelBuy();
    },
    acceptAuc(context, bet){
      context.state.game.auction.accept(bet);           
      if(context.state.game.auction.isEnd){
        context.commit('returnFromAuction');
      }
    },
    cancelAuc(context){
      context.state.game.auction.cancel();
      if(context.state.game.auction.isEnd){
        context.commit('returnFromAuction');
      }
    },
    payFine: (context) => context.state.game.payFine(),
    toLay: (contex, cardIndex) => contex.state.game.toLay(cardIndex),
    redeem: (context, cardIndex) => context.state.game.redeem(cardIndex),
    byeHouse: (context, card) => context.state.game.byeHouse(card),
    saleHouse: (context, card) => context.state.game.saleHouse(card),
    swapCard: (context, {saler, customer}) => context.state.game.swapCard(saler, customer),
    gameOver: (context, id) => context.state.game.gameOver(id),          
  },
  modules: {
  },
  getters: {  
    checkJail({game}, getters){
      return game.jail.findIndex(player=>player.id === getters.currentPlayer.id) !== -1; 
    },
    currentPlayer({game}){
      return game.players.filter(pl=>pl.id===game.whoGo)[0];
    },
    getOwner({game}, getters){
      return game._findPlayer(getters.currentCard.owner) || null;
    },
    currentCard({game}, getters){
      if(game.currentSpecialCard){
        return game.currentSpecialCard;
      }
      return game.board[getters.currentPlayer.position];
    },    
  }
})
